/* CSS Variables */
/* Variables */
/* Site Height */
$--navigation-height: 50px;
$--footer-height: $--navigation-height;
$--top-height: 0px;
$--bottom-height: 0px;
/* Background */
$--bg-darker-1: 0.7;
$--bg-darker-2: 0.9;
/* Text */
$--font-default: 12px;
/* Root defaults */
.css-hyum1k-MuiToolbar-root {
  min-height: auto !important;
  padding: 0 !important;
}
.css-dnrpxu-MuiCardActions-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* HMTL element */
html {
  background-image: url("../assets/image/background/site-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  /* Body element */
  body {
    background: linear-gradient(
        rgba(0, 0, 0, $--bg-darker-1),
        rgba(0, 0, 0, $--bg-darker-1)
      ),
      rgba(65, 65, 65, 0.65);
    justify-content: center;
    min-width: 400px;
    margin: 0;
  }
  /**/
  .body {
    justify-content: center;
    /**/
    .bubble {
      background-color: rgba(33,55,75,1);
      border: 1px solid rgba(85,99,97,0.7);
      position: fixed;
      z-index: 1050;
      bottom: 1vh;
      height: 3vh;
      right: 1vw;
      width: 3vh;
      /**/
      svg {
        position: absolute;
        height: 2vh;
        width: 2vh;
      }
      /**/
      &:hover {
        background-color: rgba(33,55,75,0.5);
        border: 1px solid rgba(85,99,97,0.3);
      }
      /**/
      & div.menu {
        border: 1.5px double rgb(155, 155, 155);
        background: rgba(65, 65, 65, 1);
        border-radius: 10%;
        position: absolute;
        min-width: 100px;
        width: calc(100vw / 12 - 3px - 3vh);
        bottom: 3vh;
        right: 0;
      }
    }
    /**/
    &Margin {
      border: none;
    }
    /* Upper body element. Content area + Left and Right columns */
    &Column {
      border-right: 1.5px double rgb(155, 155, 155);
      border-left: 1.5px double rgb(155, 155, 155);
      background: rgba(65, 65, 65, 0.35);
      min-height: calc(
        100vh - $--navigation-height - $--top-height - $--bottom-height -
          $--footer-height
      );
      position: relative;
      max-width: 1920px;
      padding: 0;
      /* Content area */
      .bodyContent {
        justify-content: flex-start;
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
        display: flex;
        width: 100%;
        /**/
        &Edit {
          @extend .bodyContent;
          opacity: 0.6;
        }
        /* Page Header */
        img.header {
          height: calc(360px - $--navigation-height - $--top-height);
          //margin: -1.5vh 0 calc((3vh + 17px) * -1) 0 !important;
          margin: 0 0 calc((3vh + 17px) * -1) 0 !important;
          border: 5px ridge rgba(171, 150, 111, 1);
          object-position: 20% 100%;
          object-fit: cover;
          border-radius: 0;
          width: 100%;
        }
        video.header {
          height: calc(360px - $--navigation-height - $--top-height);
          //margin: -1.5vh 0 calc((3vh + 17px) * -1) 0 !important;
          border: 5px ridge rgba(171, 150, 111, 1);
          margin: 0 0 -2.5px 0 !important;
          object-position: 50% 15%;
          object-fit: cover;
          border-radius: 0;
          z-index: 1049;
          width: 100%;
        }
        /**/
        .dotsHeader {
          position: absolute;
          z-index: 1050;
          height: 25px;
          width: 25px;
          right: 10px;
          top: 5px;
          & svg {
          filter: invert(14%) sepia(47%) saturate(4192%) hue-rotate(350deg) brightness(92%) contrast(125%);
          }
          /**/
          & svg:hover {
            filter: invert(40%) sepia(41%) saturate(7463%) hue-rotate(343deg) brightness(111%) contrast(102%);
            cursor: pointer;
          }
          /**/
          & div.menu {
            border: 1.5px double rgba(155, 155, 155, 1);
            background: rgba(65, 65, 65, 1);
            position: absolute;
            right: 4.165px;
            top: 16.67px;
            padding: 0;
            /**/
            & button {
              min-width: min-content;
              padding: 9px 12px;
              &:hover {
                background: rgb(87, 87, 87);
              }
            }
          }
        }
        /**/
        & .content {
          justify-content: center;
          align-items: center;
          display: flex;
          width: 100%;
          /**/
          &Column {
            @extend .content;
            flex-direction: column;
          }
          /**/
          &Row {
            @extend .content;
            margin: 0 !important;
            /**/
            &Start {
              @extend .contentRow;
              justify-content: flex-start;
            }
            /**/
            & div {
              @extend .content;
              /**/
              & > img {
                border: 2.5px ridge rgba(171, 150, 111, 1);
                object-position: 20% 100%;
                width: calc(100% - 5vw);
                border-radius: 10px;
                object-fit: cover;
                margin: 0 2.5vw;
              }
              /**/
              & > video {
                border: 2.5px ridge rgba(171, 150, 111, 1);
                object-position: 20% 100%;
                width: calc(100% - 5vw);
                border-radius: 10px;
                object-fit: cover;
                margin: 0 2.5vw;
              }
            }
            /**/
            & svg {
              filter: invert(14%) sepia(47%) saturate(4192%) hue-rotate(350deg) brightness(92%) contrast(125%);
              position: absolute;
              height: 4vw;
              width: 4vw;
              /**/
              &:hover {
                filter: invert(40%) sepia(41%) saturate(7463%) hue-rotate(343deg) brightness(111%) contrast(102%);
                cursor: pointer;
              }
              /**/
              &.h2 {
                height: calc(max(1.6666666667vw, 22px) * 1.167);
                width: calc(max(1.6666666667vw, 22px) * 1.167);
              }
            }
          }
        }
      }
    }
  }
}
/* Collapse  */
.collapseArea {
  /* Page Cards */
  & > div {
    position: relative;
    margin: 2vh 0;
  }
}
/* Horizontal line styles */
hr {
  border: 1px inset rgba(171, 150, 111, 0.5);
  margin: 5vw;
  width: 100%;
  /* Make horizontal line invisible */
  &.space {
    margin: 2.5vw;
    opacity: 0;
  }
  /* Make horizontal line as appbar link mediator */
  &.mediator {
    width: 100%;
    opacity: 0;
    margin: 0;
  }
}
/* Link */
a {
  min-width: auto !important;
  padding: 0 !important;
}
